.block__container {
  width: 729px;
  height: 574px;
  background-color: rgba(41, 41, 41, 0.8);
  font-weight: 300;
  font-size: 30px;
  line-height: 35px;
  color: #ffffff;
  margin-top: 240px;
  margin-left: 100px;
  display: flex;
  flex-wrap: wrap;
  padding-left: 20px;
  padding-right: 20px;

  .destination {
    width: 100%;
    height: 110px;
    margin-top: 70px;
  }

  .date {
    width: 100%;
    height: 110px;
    margin-top: 30px;
    margin-bottom: 80px;
  }

  .click {
    margin: 0 0 0 auto;

    button {
      width: 323px;
      height: 60px;
      background: #ffa800;
      border-radius: 5px;
      font-weight: bold;
      font-size: 24px;
      line-height: 28px;
      text-align: center;
      text-transform: uppercase;
      color: #292929;
    }
  }
}
