.seat__container {
  display: flex;
  flex-wrap: nowrap;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  margin-bottom: 30px;
}

.seat__icon {
  margin-right: 34px;
}

.seat__text {
  width: 105px;
  margin-right: 35px;
}

.ant-switch {
  margin: 0;
  padding: 0;
  color: rgba(255, 168, 0, 1);
  background-color: rgba(255, 255, 255, 1);
  font-size: 14px;
  font-variant: tabular-nums;
  list-style: none;
  font-feature-settings: "tnum";
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  min-width: 72px;
  height: 19px;
  line-height: 22px;
  vertical-align: middle;
  border: 0;
  border-radius: 100px;
  cursor: pointer;
  transition: all 0.2s;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.ant-switch-checked {
  background: rgba(252, 220, 157, 1);
}

.ant-switch-checked .ant-switch-handle {
  left: calc(100% - 28px);
}

.ant-switch-checked .ant-switch-handle::before {
  background-color: rgba(255, 168, 0, 1);
}

.ant-switch-handle {
  position: absolute;
  top: -5px;
  left: 0;
  width: 28px;
  height: 28px;
  transition: all 0.2s ease-in-out;
}

.ant-switch-handle::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(196, 196, 196, 1);

  border-radius: 50%;
  box-shadow: 0 2px 4px 0 rgba(0, 35, 11, 0.2);
  transition: all 0.2s ease-in-out;
  content: "";
}
