  position: absolute;
  z-index: 2;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: grid;
  width: 100%;
  height: 100%;
  background-color: transparent;
  grid-template-columns: 42px 32px 64px 1fr 78px;
