.informationBlock__seatsCount {
  height: 337px;
  border-bottom: 1px dashed #c4c4c4;
  padding-top: 40px;
  padding-bottom: 49px;
}

.seatsCount__title {
  font-weight: bold;
  font-size: 30px;
  line-height: 35px;
  color: #292929;
  padding-left: 20px;
  padding-bottom: 15px;
}

.seatsCount__blocks {
  display: flex;
  height: 210px;
}

.seatsCount__block {
  width: calc(100% / 3);
  padding-top: 20px;
  padding-left: 36px;
  padding-right: 36px;
}

.seatsCount__block:hover {
  border: 1px solid rgba(255, 168, 0, 0.79);
  background: #f7f6f6;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
}

.countBlock__field {
  width: 250px;
  height: 50px;
  border: 1px solid #928f94;
  border-radius: 5px;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  color: #292929;
  text-align: center;
  padding-top: 15px;
  background: #fff;
  margin-bottom: 23px;
}

.countBlock__info {
  width: 240px;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}

.countBlock__info.one-seat {
  border: none;
  font-size: 16px;
  line-height: 19px;
  color: #928f94;
}
